.community-involvement {
    padding: 40px;
    background-color: #ffffff;
    color: #000000;
    border-top: 2px solid #ddd;
    border-bottom: 2px solid #ddd;
  }
  
  .community-involvement h1 {
    font-size: 2em;
    color: #333333;
    margin-bottom: 20px;
    text-align: left;
  }
  
  .involvement-item {
    margin-bottom: 20px;
  }
  
  .involvement-item h2 {
    font-size: 1.5em;
    color: #444444;
    margin-bottom: 8px;
  }
  
  .involvement-dates {
    font-size: 1em;
    color: #666666;
    margin-bottom: 15px;
    font-style: italic;
  }
  
  .involvement-description {
    list-style-type: disc;
    margin-left: 20px;
    padding-left: 15px;
    line-height: 1.8;
    font-size: 1em;
    color: #333333;
  }
  
  .involvement-description li {
    margin-bottom: 10px;
  }
  
  @media screen and (max-width: 768px) {
    .community-involvement {
      padding: 20px;
    }
  
    .involvement-item h2 {
      font-size: 1.3em;
    }
  
    .involvement-dates,
    .involvement-description {
      font-size: 0.95em;
    }
  }
  
  @media screen and (max-width: 480px) {
    .community-involvement {
      padding: 15px;
    }
  
    .community-involvement h1 {
      font-size: 1.6em;
    }
  
    .involvement-item h2 {
      font-size: 1.2em;
    }
  
    .involvement-description {
      font-size: 0.9em;
    }
  }
  