.projects {
    padding: 40px;
    background-color: #ffffff;
    color: #000000;
    border-top: 2px solid #ddd;
    border-bottom: 2px solid #ddd;
}

.projects h1 {
    font-size: 2em;
    color: #333333;
    margin-bottom: 20px;
}

.project-item {
    margin-bottom: 30px;
}

.project-item h2 {
    font-size: 1.5em;
    color: #444444;
    margin-bottom: 10px;
}

.project-dates {
    font-size: 1em;
    color: #888888;
    margin-bottom: 15px;
}

.project-description {
    list-style-type: disc;
    margin-left: 20px;
    padding-left: 15px;
    line-height: 1.8;
    font-size: 1em;
    color: #333333;
}

.project-description li {
    margin-bottom: 10px;
}

@media screen and (max-width: 768px) {
    .projects {
        padding: 20px;
    }

    .project-item h2 {
        font-size: 1.3em;
    }

    .project-dates,
    .project-description {
        font-size: 0.95em;
    }
}

@media screen and (max-width: 480px) {
    .projects {
        padding: 15px;
    }

    .project-item h2 {
        font-size: 1.2em;
    }

    .project-description {
        font-size: 0.9em;
    }
}
